  height: 100%;
  width: 100%; }

h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 35px; }

h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 20px;
  letter-spacing: 1.43px; }

h4 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px; }

svg text {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  fill: #2a2a2a;
  font-family: Roboto Mono, Monaco, monospace; }

svg text::-moz-selection {
  background: none; }

svg text::selection {
  background: none; }

.btn[disabled] {
  cursor: default; }

input[type="checkbox"] {
  cursor: pointer; }

button {
  font-weight: 400;
  outline: none !important; }

.default {
  border: 1px solid #a8a8a8;
  color: #65ccff;
  border-radius: 3px;
