    flex-direction: row; }

#BlankPage {
  width: 100%;
  height: 100%;
  background-color: #f3f3f3; }
  #BlankPage img {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 70%;
    display: block;
    margin: auto;
    max-width: 650px; }

#part-explorer {
  height: 100%; }
  #part-explorer .seq-viewers-container {
    display: flex;
    height: inherit; }

label {
  width: 100%;
  font-weight: 400; }

.select {
  background-color: transparent;
  outline: none;
  font-size: 13px;
  height: 30px;
  width: 168px;
  padding-left: 5px;
  border: 1px solid #bebebe;
  padding: 5px 10px;
  color: black;
  float: right; }

.input {
  width: auto;
  padding-left: 5px;
  color: black;
  background-color: #f0f0f0;
  border: none;
  padding: 5px 10px;
  border-radius: 1px;
  margin-left: auto;
  outline: none; }

.modal {
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  background-color: white;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-bottom: 0px; }

.scroll::-webkit-scrollbar {
  width: 7px; }

.scroll::-webkit-scrollbar-thumb {
  background-clip: padding-box;
  border-radius: 7px;
  background-color: #a6a6a6; }

.scroll::-webkit-scrollbar-track {
  border-radius: 20px; }

.scroll::-webkit-scrollbar-track-piece {
